import { useState } from "react";
import { Mutation } from "react-apollo";
import gql from "graphql-tag";

const SIGNIN_MUTATION = gql`
  mutation SIGNIN_MUTATION($email: String!, $password: String!) {
    authenticateUser(email: $email, password: $password) {
      id
      token
    }
  }
`;

const SignIn = () => {
  const [emailRef, emailValue] = useHandleInput();
  const [passwordRef, passwordValue] = useHandleInput();

  return (
    <div>
      <form
        onSubmit={e => {
          e.preventDefault();
        }}
      >
        <input value={emailValue} ref={emailRef} type="email" required />
        <input value={passwordValue} ref={passwordRef} type="password" required />
        <button type="submit">Sign in</button>
      </form>
    </div>
  );
};

export default SignIn;
